

class Skier {
  constructor(){
    this.width = 50;
    this.height = 50;
    this.pos = {x: 400, y: 200};
    this.velocity = {x: 0, y: 0};
    this.color = "#f6fa0a";
    this.lives = 3;
  }

  resetPosition(){
    this.color = "#f6fa0a";
    this.pos = {x: 400, y: 200};
  }

  draw(ctx){
    ctx.beginPath();
    ctx.fillStyle = this.color;
    ctx.fillRect(this.pos.x, this.pos.y, this.width, this.height);
  }

  clear(ctx){
    ctx.clearRect(this.pos.x, this.pos.y, this.width, this.height);
  }

  falls(ctx){
    this.lives -= 1;
    this.clear(ctx);
    this.color = "#000000";
    this.draw(ctx);
  }

  moveRight(ctx){
    this.color = "#0af6fa";
    this.clear(ctx);
    this.draw(ctx);
  }

  moveLeft(ctx){
    this.color = "#7efa0a";
    this.clear(ctx);
    this.draw(ctx);
  }

  moveDown(ctx){
    this.color = "#fa0a7e";
    this.clear(ctx);
    this.draw(ctx);
  }

  collision(otherObject){
    if (this.pos.x < otherObject.pos.x + otherObject.width && this.pos.x + this.width  > otherObject.pos.x &&
		this.pos.y < otherObject.pos.y + otherObject.height && this.pos.y + this.height > otherObject.pos.y) {
      return true;
    } else {
      return false;
    }
  }


}

export default Skier;



